import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import aboutusList from '@/views/pages/aboutus/list'
import aboutusDetail from '@/views/pages/aboutus/formModel'
import aboutusAdd from '@/views/pages/aboutus/formAdd'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import meishifenleiList from '@/views/pages/meishifenlei/list'
import meishifenleiDetail from '@/views/pages/meishifenlei/formModel'
import meishifenleiAdd from '@/views/pages/meishifenlei/formAdd'
import meishixinxiList from '@/views/pages/meishixinxi/list'
import meishixinxiDetail from '@/views/pages/meishixinxi/formModel'
import meishixinxiAdd from '@/views/pages/meishixinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import newsList from '@/views/pages/news/list'
import cartList from '@/views/pages/shop_order/cart'
import addressList from '@/views/pages/shop_address/list'
import shangjiaList from '@/views/pages/shangjia/list'
import shangjiaDetail from '@/views/pages/shangjia/formModel'
import shangjiaAdd from '@/views/pages/shangjia/formAdd'
import bingrenzhuanquList from '@/views/pages/bingrenzhuanqu/list'
import bingrenzhuanquDetail from '@/views/pages/bingrenzhuanqu/formModel'
import bingrenzhuanquAdd from '@/views/pages/bingrenzhuanqu/formAdd'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'aboutusList',
			component: aboutusList
		}, {
			path: 'aboutusDetail',
			component: aboutusDetail
		}, {
			path: 'aboutusAdd',
			component: aboutusAdd
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'meishifenleiList',
			component: meishifenleiList
		}, {
			path: 'meishifenleiDetail',
			component: meishifenleiDetail
		}, {
			path: 'meishifenleiAdd',
			component: meishifenleiAdd
		}
		, {
			path: 'meishixinxiList',
			component: meishixinxiList
		}, {
			path: 'meishixinxiDetail',
			component: meishixinxiDetail
		}, {
			path: 'meishixinxiAdd',
			component: meishixinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'addressList',
			component: addressList
		}
		, {
			path: 'shangjiaList',
			component: shangjiaList
		}, {
			path: 'shangjiaDetail',
			component: shangjiaDetail
		}, {
			path: 'shangjiaAdd',
			component: shangjiaAdd
		}
		, {
			path: 'bingrenzhuanquList',
			component: bingrenzhuanquList
		}, {
			path: 'bingrenzhuanquDetail',
			component: bingrenzhuanquDetail
		}, {
			path: 'bingrenzhuanquAdd',
			component: bingrenzhuanquAdd
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
