
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"80%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator=">" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="美食名称" prop="meishimingcheng">
						<el-input class="list_inp" v-model="form.meishimingcheng" placeholder="美食名称"
							 type="text" 							:readonly="!isAdd||disabledForm.meishimingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="美食图片" prop="meishitupian">
						<uploads
							:disabled="!isAdd||disabledForm.meishitupian?true:false"
							action="file/upload" 
							tip="请上传美食图片" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.meishitupian?form.meishitupian:''" 
							@change="meishitupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="美食分类" prop="meishifenlei">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.meishifenlei?true:false"
							v-model="form.meishifenlei" 
							placeholder="请选择美食分类"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in meishifenleiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="口味" prop="kouwei">
						<el-input class="list_inp" v-model="form.kouwei" placeholder="口味"
							 type="text" 							:readonly="!isAdd||disabledForm.kouwei?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="食材" prop="shicai">
						<el-input class="list_inp" v-model="form.shicai" placeholder="食材"
							 type="text" 							:readonly="!isAdd||disabledForm.shicai?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="做法" prop="zuofa">
						<el-input class="list_inp" v-model="form.zuofa" placeholder="做法"
							 type="text" 							:readonly="!isAdd||disabledForm.zuofa?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="价格" prop="price">
						<el-input class="list_inp" v-model.number="form.price" placeholder="价格"
							 type="number" 							:readonly="!isAdd||disabledForm.price?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="商家账号" prop="shangjiazhanghao">
						<el-input class="list_inp" v-model="form.shangjiazhanghao" placeholder="商家账号"
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiazhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="商家名称" prop="shangjiamingcheng">
						<el-input class="list_inp" v-model="form.shangjiamingcheng" placeholder="商家名称"
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiamingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="联系电话" prop="lianxidianhua">
						<el-input class="list_inp" v-model="form.lianxidianhua" placeholder="联系电话"
							 type="text" 							:readonly="!isAdd||disabledForm.lianxidianhua?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="商家地址" prop="shangjiadizhi">
						<el-input class="list_inp" v-model="form.shangjiadizhi" placeholder="商家地址"
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiadizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="购买须知" prop="goumaixuzhi">
						<el-input v-model="form.goumaixuzhi" placeholder="购买须知" type="textarea"
						:readonly="!isAdd||disabledForm.goumaixuzhi?true:false"
						/>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="美食详情" prop="meishixiangqing">
						<editor class="list_editor" :value="form.meishixiangqing" placeholder="请输入美食详情" :readonly="!isAdd||disabledForm.meishixiangqing?true:false"
							@change="(e)=>editorChange(e,'meishixiangqing')"></editor>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'bingrenzhuanqu'
	const formName = '病人专区'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		meishimingcheng: '',
		meishitupian: '',
		meishifenlei: '',
		kouwei: '',
		shicai: '',
		zuofa: '',
		goumaixuzhi: '',
		meishixiangqing: '',
		storeupnum: '0',
		clicknum: '0',
		price: '0',
		shangjiazhanghao: '',
		shangjiamingcheng: '',
		lianxidianhua: '',
		shangjiadizhi: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		meishimingcheng : false,
		meishitupian : false,
		meishifenlei : false,
		kouwei : false,
		shicai : false,
		zuofa : false,
		goumaixuzhi : false,
		meishixiangqing : false,
		storeupnum : false,
		clicknum : false,
		price : false,
		shangjiazhanghao : false,
		shangjiamingcheng : false,
		lianxidianhua : false,
		shangjiadizhi : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		meishimingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		meishitupian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		meishifenlei: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		kouwei: [
		],
		shicai: [
		],
		zuofa: [
		],
		goumaixuzhi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		meishixiangqing: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		clicknum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		price: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		shangjiazhanghao: [
		],
		shangjiamingcheng: [
		],
		lianxidianhua: [
		],
		shangjiadizhi: [
		],
	})
	//美食图片上传回调
	const meishitupianUploadSuccess=(e)=>{
		form.value.meishitupian = e
	}
	//美食分类列表
	const meishifenleiLists = ref([])
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.meishixiangqing = res.data.data.meishixiangqing.replace(reg,'../../../cl18664188/file');
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='meishimingcheng'){
					form.value.meishimingcheng = row[x];
					disabledForm.value.meishimingcheng = true;
					continue;
				}
				if(x=='meishitupian'){
					form.value.meishitupian = row[x];
					disabledForm.value.meishitupian = true;
					continue;
				}
				if(x=='meishifenlei'){
					form.value.meishifenlei = row[x];
					disabledForm.value.meishifenlei = true;
					continue;
				}
				if(x=='kouwei'){
					form.value.kouwei = row[x];
					disabledForm.value.kouwei = true;
					continue;
				}
				if(x=='shicai'){
					form.value.shicai = row[x];
					disabledForm.value.shicai = true;
					continue;
				}
				if(x=='zuofa'){
					form.value.zuofa = row[x];
					disabledForm.value.zuofa = true;
					continue;
				}
				if(x=='goumaixuzhi'){
					form.value.goumaixuzhi = row[x];
					disabledForm.value.goumaixuzhi = true;
					continue;
				}
				if(x=='meishixiangqing'){
					form.value.meishixiangqing = row[x];
					disabledForm.value.meishixiangqing = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='clicknum'){
					form.value.clicknum = row[x];
					disabledForm.value.clicknum = true;
					continue;
				}
				if(x=='price'){
					form.value.price = row[x];
					disabledForm.value.price = true;
					continue;
				}
				if(x=='shangjiazhanghao'){
					form.value.shangjiazhanghao = row[x];
					disabledForm.value.shangjiazhanghao = true;
					continue;
				}
				if(x=='shangjiamingcheng'){
					form.value.shangjiamingcheng = row[x];
					disabledForm.value.shangjiamingcheng = true;
					continue;
				}
				if(x=='lianxidianhua'){
					form.value.lianxidianhua = row[x];
					disabledForm.value.lianxidianhua = true;
					continue;
				}
				if(x=='shangjiadizhi'){
					form.value.shangjiadizhi = row[x];
					disabledForm.value.shangjiadizhi = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			form.value.clicknum='0'
			form.value.price='0'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('shangjiazhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shangjiazhanghao = json.shangjiazhanghao
				disabledForm.value.shangjiazhanghao = true;
			}
			if(json.hasOwnProperty('shangjiamingcheng') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shangjiamingcheng = json.shangjiamingcheng
				disabledForm.value.shangjiamingcheng = true;
			}
			if(json.hasOwnProperty('lianxidianhua') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.lianxidianhua = json.lianxidianhua
				disabledForm.value.lianxidianhua = true;
			}
			if(json.hasOwnProperty('shangjiadizhi') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shangjiadizhi = json.shangjiadizhi
				disabledForm.value.shangjiadizhi = true;
			}
		})
		context?.$http({
			url: `option/meishifenlei/meishifenlei`,
			method: 'get'
		}).then(res=>{
			meishifenleiLists.value = res.data.data
		})
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//富文本数据回调
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		if(form.value.meishitupian!=null) {
			form.value.meishitupian = form.value.meishitupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 1px solid #eee;
		border-radius: 0px;
		padding: 40px 7% 50px;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ff879850;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				// 下拉框
				.list_sel {
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ff879850;
